@media (max-width: 500px) {
  .udent_input {
    margin-bottom: 25px;
    padding: 0px;
  }
  .form-row > .col,
  .form-row > [class*="col-"] {
    padding-right: 0px;
    padding-left: 0px;
  }
  .Student_cap {
    margin-left: 8px;
    text-align: left;
  }
  .capcha {
    width: fit-content;
    height: 38px;
    border: 1px solid #29517c;
    box-sizing: border-box;
    border-radius: 7px;
    margin-left: 0px;
    padding-left: 10px !important;
  }
  .Student_capcha {
    text-align: left;
    margin-left: -10px;
    align-items: unset;
  }
  .border-capcha {
    border-radius: 7px;
    border: 1px solid #29517c;
    padding: 10px 10px 10px 10px;
    margin-left: 0px;
  }
  .desk {
    display: none;
  }
  .mobile {
    height: 468px;
    background: #f2f2f2;
    width: 100%;
    margin-bottom: 20px;
  }
  .row {
    margin-left: 0px !important;
  }
  .row1 {
    margin-bottom: 0px;
  }
  .row2 {
    margin-bottom: 0px;
  }
  .row3 {
    margin-bottom: 0px;
  }

  #desktop {
    display: none;
  }
  .Student_pic {
    display: none;
  }
  .Student_head_mob {
    margin-left: 20px;
    margin-top: 37px;
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    /* identical to box height */
    color: #29517c;
    text-align: left;
    margin-left: 20px;
  }
  .Student_head2_mob {
    margin-left: 20px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-style: normal;
    font-weight: 200;
    font-size: 28px;
    line-height: 33px;
    color: #29517c;
    text-align: left;
    margin-left: 20px;
  }
  .Student_img {
    margin-top: 77px;
    width: 287px;
    height: 287px;
  }
  .form-row {
    margin-left: 20px;
    margin-right: 20px;
  }
  .Student_page1 {
    align-items: left;
    margin-left: 0px;
    margin-top: 30px;
    margin-bottom: 100px;
  }
}
/* Big tablets */
@media (min-width: 800px) and (max-width: 1200px) {
  .Student_page2 {
    align-items: left;
    margin-left: 0px;
    margin-top: 30px;
    margin-bottom: 100px;
  }
  .SFSecound_row {
    height: 100vh;
  }
}
.Student_pic {
  /* height: 100vh; */
}
@media (min-width: 510px) {
  .mobile {
    display: none;
  }
  .mob {
    display: none;
  }
  .Student_head2_mob {
    margin-left: 20px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-style: normal;
    font-weight: 200;
    font-size: 28px;
    line-height: 33px;
    color: #29517c;
    text-align: left;
    margin-left: 20px;
  }
}
@media (min-width: 500px) and (max-width: 800px) {
  .Student_page1 {
    margin-left: 30px;
    margin-top: 30px !important;
    text-align: right;
  }
}
/* Tablet */
@media only screen and (min-width: 481px) and (max-width: 800px) {
  .SFSecound_row {
    height: 100vh;
  }
  .Student_head2 {
    font-size: 35px;
  }
  .Student_page2 {
    margin-top: 70px;
  }
  .Student_img {
    padding-top: 100px;
    height: 300px;
    width: 300px;
  }
  .form-row {
    margin-left: 20px;
    margin-right: 20px;
  }
}
/* mobile */
@media screen and (max-width: 480px) {
  .Student_page2 {
    margin-top: 30px;
    margin-bottom: 100px;
  }
  .StudentMobile_mp {
    margin-top: 20px;
  }
  .Student_add {
    margin-bottom: 0;
  }
  .personalDOB {
    height: 2.5rem;
    width: 21rem;
    border-radius: 7px;
    border: 1px solid #29507c;
  }
}
.personalDOB {
  height: 2.5rem;
  border-radius: 7px;
  border: 1px solid #29507c;
}
